<template>
  <div>
<nav-bar :name="title"></nav-bar>
        <v-container class="text-center">
            <router-link to="/addinvoice" v-if="isStaff">
                <v-btn color="#E47911" large height="40px" dark class="mr-2 mb-2">
                    <v-icon
                        left
                        dark
                    > mdi-plus 
                    </v-icon>  Add invoice
                </v-btn>
            </router-link>
            <router-link to="/addextrainvoice" v-if="isStaff">
                <v-btn color="#E47911" large height="40px" dark class="mb-2">
                    <v-icon
                        left
                        dark
                    > mdi-plus 
                    </v-icon>  Add extra invoice
                </v-btn>
            </router-link>
        </v-container>

          <v-card class="ma-auto pb-5" width="90%" outlined>
            <v-container class="text-center">
                <v-card>
            <v-layout>
            <v-col cols="10" class="text-right" v-if="isStaff">
              <v-btn
                class="mr-1"
                icon
                right
                @click="activeExport"
              >
                <v-icon size="24px" :color="export_btn_color">
                  mdi-file-move
                </v-icon>
              </v-btn>
            </v-col>
            <v-col cols="2" class="text-right" v-if="isStaff">
              <v-btn
                class="mr-1"
                icon
                right
                @click="activeDelete"
              >
                <v-icon size="24px" :color="delete_btn_color">
                  mdi-delete
                </v-icon>
              </v-btn>
            </v-col>
            </v-layout>

            <v-card-title>
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-card-title>
              <v-data-table
                :headers="headers"
                :search="search"
                :items="invoices"
                :loading="loading"
                loading-text="Loading... Please wait"
                hide-default-footer
                :page.sync="page"
                :items-per-page="itemsPerPage"
                @page-count="pageCount = $event"
                >
                <template v-slot:item.status="{ item }">
                  <v-chip
                    :color="getColor(item.status)"
                    dark
                  >
                    {{ item.status | name_status }}
                  </v-chip>
                </template>
                <template v-slot:item.unique_id="{ item }">
                  <v-menu
                    top
                    offset-x
                    v-if="select"
                  >
                    <template v-slot:activator="{ on, attrs }">
                    <v-btn icon
                      dark
                      v-bind="attrs"
                      v-on="on"
                    >
                        <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                    </v-btn>
                    </template>
                    <v-list>
                    <v-list-item link v-if="isStaff">
                      <v-list-item-title>
                        <router-link :to="{ name: 'UpdateInvoice', params: { 'id': item.unique_id }}"> Update invoice </router-link>
                      </v-list-item-title>
                    </v-list-item>
                    <v-list-item @click="download_invoice(item.unique_id)" link>
                      <v-list-item-title> Download invoice </v-list-item-title>
                    </v-list-item>
                    </v-list>
                  </v-menu>
                    <v-checkbox
                      :value="item.unique_id"
                      color="#E47911"
                      v-model="idInvoice"
                      v-else
                      class="my-n3"
                    ></v-checkbox>
                </template>
              </v-data-table>
                </v-card>
                <br>
              <v-pagination
                v-model="page"
                :length="pageCount"
              ></v-pagination>

              <v-row v-if="!select">
          <v-col>
          <v-btn
          class="mx-4 my-2"
          color="#989898"
          dark
          @click="desactivarBtn"
          >
          CANCEL
          </v-btn>
          <v-btn
          class="mx-4 my-2"
          color="#E47911"
          dark
          v-if="change"
          @click="export_invoices"
          :loading="loading_exp"
          >
          EXPORT
          </v-btn>
          <v-btn
          class="mx-4 my-2"
          color="#E47911"
          dark
          v-if="!change"
          @click="delete_invoices"
          :loading="loading_del"
          >
          DELETE
          </v-btn>
          </v-col>
        </v-row>

        </v-container>
          </v-card>

<footer-nav></footer-nav>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import InvoiceService from '@/services/InvoiceService';
  import Swal from 'sweetalert2'

export default {
    components: {
      NavBar, FooterNav
    },

    data: () => ({
      title: 'INVOICES',
      select: true,
      change: true,
      export_btn_color: '#000',
      delete_btn_color: '#000',
      loading: true,
      loading_exp: false,
      loading_del: false,
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      search: '',
      invoices: [

      ],
      idInvoice: [],
      headers: [
        {
          text: 'Name',
          value: 'store.name'
        },
        {
          text: 'Amount',
          value: 'amount'
        },
        { 
          text: 'Status',
          value: 'status'
        },
        { 
          text: 'Date',
          value: 'due_date'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
    }),

    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },

      idStore(){
        if(!this.isStaff)
          return this.$store.state.auth.store_user.unique_id;
        
        return '';
      }
    },
    
    mounted(){
      this.refresh_list();
    },

    filters:{
      name_status(value){
        if (value == "OK") return 'Paid'
        else if (value == 'P') return 'Pending'
        else return 'Other'
      }
    },
      
    methods: {
      activeExport(){
          this.change = true;
          this.select = !this.select;
          this.export_btn_color = this.select ? '#000' : '#E47911';
          this.delete_btn_color = '#000'
      },

      activeDelete(){
          this.change = false;
          this.select = !this.select;
          this.delete_btn_color = this.select ? '#000' : '#E47911';
          this.export_btn_color = '#000'
      },

      desactivarBtn(){
          this.select = !this.select;
          this.export_btn_color = '#000';
          this.delete_btn_color = '#000'
      },

      refresh_list(){
          this.loading = true
        if(this.isStaff){
          InvoiceService
            .list_admin()
            .then(res => {
              this.invoices = res;
              this.loading = false
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
        else {
          InvoiceService
            .list_invoices(this.idStore)
            .then(res => {
              this.invoices = res;
              this.loading = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
      },

      getColor (status) {
        if (status == "OK") return 'green'
        else if (status == 'P') return 'orange'
        else return 'red'
      },

      delete_invoices(){
        this.loading_del = true;
        InvoiceService
          .delete_invoices(this.idInvoice)
          .then(res => {
            this.loading_del = false
            Swal.fire({
            icon: 'success',
            title: 'Success',
            text: res['message'],
            }).then(() => {
              this.refresh_list();
            })
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
      },

      export_invoices(){
        this.loading_exp = true;
        InvoiceService
          .export_invoices(this.idInvoice)
          .then(res => {
              const url = window.URL.createObjectURL(new Blob([res.data]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'invoices.xlsx'); //or any other extension
              document.body.appendChild(link);
              link.click();
            this.loading_exp = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
      },

      download_invoice(pk){
        Swal.fire({
          icon: 'question',
          title: 'Confirm',
          text: 'Are you sure you want to download this invoice?',
          confirmButtonText: 'YES',
          cancelButtonText: 'NO',
          showCancelButton: true
        }).then(res => {
          if(res.isConfirmed){
            InvoiceService
            .download_invoice(pk)
            .then(res => {
              const url = window.URL.createObjectURL(new Blob([res]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'invoice.pdf'); //or any other extension
              document.body.appendChild(link);
              link.click();
                Swal.fire({
                  icon: 'success',
                  title: 'Success',
                  text: 'Invoice successfully download.'
                }).then(() => {
                    this.refresh_list();
                  })
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
          }
        })
      }
  },

}
</script>